.App {
  text-align: center;
  overflow-y: auto;
}

.utt-logo{
  height: 30px;
  margin-top: 10px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.output-image{
  width: 30%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.btn-blocks{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  row-gap: 20px;
}

.output-visible{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.click-btn{
  display: flex;
  flex-direction: row;
  column-gap: 10px;
  align-items: center;
  justify-content: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
